*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primery-text-color: #757575;
  --title-text-color: #212121;
  --hover-text-color: #2196f3;
  --hero-text-color: #fff;
  --address-text-color: rgba(255, 255, 255, .6);
  --primery-bleck-color: #2f303a;
  --main-background-color: #f5f5f5;
  --section-background-color: #f5f4fa;
  --borders-color: #eee;
  --logo-bleck-color: #000;
  --transition-function: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  color: currentColor;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  background-color: var(--hero-text-color);
  color: var(--primery-text-color);
  letter-spacing: .03em;
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

.button {
  cursor: pointer;
  transition: color var(--transition-function), background-color var(--transition-function), box-shadow var(--transition-function);
  border: none;
  font-family: inherit;
}

.button:hover, .button:focus {
  color: var(--hero-text-color);
  background-color: var(--hover-text-color);
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.hero__button {
  min-width: 216px;
  color: var(--hero-text-color);
  background-color: var(--hover-text-color);
  text-align: center;
  letter-spacing: .06em;
  border-radius: 4px;
  align-items: center;
  padding: 10px 32px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.88;
}

.hero__button:hover, .hero__button:focus {
  box-shadow: none;
  background-color: #0ff;
}

.portfolio__button {
  color: var(--title-text-color);
  background-color: var(--section-background-color);
  text-align: center;
  border-radius: 4px;
  padding: 6px 22px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.62;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.logo {
  color: var(--hover-text-color);
  font-family: Raleway, sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.16667;
}

@media screen and (min-width: 1200px) {
  .logo {
    margin-right: 93px;
    font-size: 26px;
    line-height: 1.19231;
  }
}

.logo--header {
  color: var(--logo-bleck-color);
}

.logo--footer {
  color: var(--hero-text-color);
}

.menu-toggle {
  cursor: pointer;
  fill: var(--title-text-color);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-left: auto;
  padding: 0;
}

.menu-toggle:hover, .menu-toggle:focus {
  fill: var(--hover-text-color);
}

@media (min-width: 768px) {
  .menu-toggle {
    display: none;
  }
}

.menu-container {
  width: 100%;
  height: 100%;
  background-color: var(--hero-text-color);
  z-index: 9;
  flex-direction: column;
  padding: 48px 40px;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
  transform: translateX(100%);
  box-shadow: 0 4px 8px rgba(0, 0, 0, .04), 0 2px 4px rgba(0, 0, 0, .08), 0 1px 3px rgba(0, 0, 0, .16);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .menu-toggle {
  position: absolute;
  top: 10px;
  right: 15px;
}

.mobile-menu {
  margin-bottom: auto;
}

.mobile-menu__link {
  letter-spacing: .02em;
  color: var(--title-text-color);
  font-size: 40px;
  font-weight: 500;
  line-height: 1.175;
  display: block;
}

.mobile-menu__link:hover, .mobile-menu__link:focus {
  color: var(--hover-text-color);
}

.mobile-menu__item:not(:last-child) {
  margin-bottom: 32px;
}

.mobile-contacts {
  margin-bottom: 64px;
}

.mobile-contacts__item:first-child {
  letter-spacing: .02em;
  color: var(--hover-text-color);
  margin-bottom: 32px;
  font-size: 34px;
  font-weight: 500;
  line-height: 1.17647;
}

.mobile-contacts__item:last-child {
  letter-spacing: .02em;
  color: var(--primery-text-color);
  font-size: 24px;
  font-weight: 500;
  line-height: 1.16667;
}

.mobile-contacts__link {
  color: inherit;
}

.mobile-socials {
  display: flex;
}

.mobile-socials__item {
  letter-spacing: .02em;
  color: var(--hover-text-color);
  font-size: 18px;
  font-weight: 500;
  line-height: 1.22222;
}

.mobile-socials__link:after {
  content: " ";
  border: 1px solid rgba(33, 33, 33, .2);
  margin-left: 10px;
  margin-right: 10px;
  transform: rotate(90deg);
}

.backdrop {
  z-index: 1;
  width: 100%;
  height: 100%;
  transition: opacity var(--transition-function), visibility var(--transition-function);
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  width: calc(100vw - 30px);
  height: auto;
  transition: transform var(--transition-function), opacity var(--transition-function);
  background-color: var(--hero-text-color);
  border-radius: 4px;
  padding: 40px;
  position: absolute;
  top: 5vh;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 480px) {
  .modal {
    width: 450px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 528px;
  }
}

.backdrop.is-hidden .modal {
  opacity: 0;
  transform: translate(-50%, -50%)scale(.4)rotate(90deg);
}

.modal__btn {
  width: 30px;
  height: 30px;
  color: var(--logo-bleck-color);
  background-color: var(--hero-text-color);
  cursor: pointer;
  transition: color var(--transition-function), border-color var(--transition-function);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: inline-flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__btn:hover, .modal__btn:focus {
  color: var(--hover-text-color);
  border-color: var(--hover-text-color);
}

.modal__exclusion-icon {
  fill: currentColor;
}

.modal__strong {
  text-align: center;
  color: var(--title-text-color);
  margin-bottom: 12px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.5;
  display: block;
}

@media screen and (min-width: 1200px) {
  .modal__strong {
    line-height: 1.15;
  }
}

.modal__label {
  letter-spacing: .01em;
  color: var(--primery-text-color);
  font-size: 12px;
  line-height: 1.16667;
}

.modal__wraper {
  margin-top: 4px;
  margin-bottom: 10px;
  position: relative;
}

.modal__input {
  width: 100%;
  min-height: 40px;
  transition: border var(--transition-function), fill var(--transition-function);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: none;
  padding: 12px 12px 12px 42px;
}

.modal__input:not(:placeholder-shown):invalid:required {
  border: 1px solid #ea1f00;
}

.modal__input:not(:placeholder-shown):invalid:required + .modal__icon {
  fill: #ea1f00;
}

.modal__input:not(:placeholder-shown):valid:required {
  border: 1px solid #2fa700;
}

.modal__input:not(:placeholder-shown):valid:required + .modal__icon {
  fill: #2fa700;
}

.modal__input:focus {
  border: 1px solid var(--hover-text-color);
}

@media screen and (min-width: 480px) {
  .modal__input {
    min-width: 370px;
  }
}

@media screen and (min-width: 1200px) {
  .modal__input {
    min-width: 448px;
  }
}

.modal__icon {
  fill: var(--title-text-color);
  transition: fill var(--transition-function);
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
}

.modal__input:focus + .modal__icon {
  fill: var(--hover-text-color);
}

.modal__comment {
  width: 100%;
  resize: none;
  letter-spacing: .01em;
  transition: border-color var(--transition-function);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: none;
  margin-top: 4px;
  margin-bottom: 20px;
  padding: 12px 16px;
  font-size: 12px;
  line-height: 1.16667;
  display: block;
}

.modal__comment:not(:placeholder-shown):valid:required {
  border-color: #2fa700;
}

.modal__comment:focus {
  border-color: var(--hover-text-color);
}

.checkbox {
  justify-content: center;
  margin-bottom: 30px;
  display: flex;
  position: relative;
}

.checkbox__field {
  color: var(--primery-text-color);
